// src/app/components/BookingForm.js
'use client';

import { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';

export default function BookingForm({ tour, onClose }) {
  const [formData, setFormData] = useState({
    startDate: null,
    guests: 1,
    fullName: '',
    email: '',
    phone: '',
    specialRequests: '',
    paymentMethod: 'credit',
    agreeToTerms: false
  });

  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (e) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const validateForm = () => {
    const newErrors = {};
    
    if (!formData.startDate) newErrors.startDate = 'Please select a date';
    if (formData.guests < 1) newErrors.guests = 'At least 1 guest required';
    if (!formData.fullName.trim()) newErrors.fullName = 'Full name is required';
    if (!formData.email.includes('@')) newErrors.email = 'Valid email required';
    if (!formData.phone.match(/^[0-9]{10,15}$/)) newErrors.phone = 'Valid phone number required';
    if (!formData.agreeToTerms) newErrors.agreeToTerms = 'You must accept the terms';
    
    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!validateForm()) return;
    
    setIsSubmitting(true);
    
    try {
      // Simulate API call
      await new Promise(resolve => setTimeout(resolve, 1500));
      
      alert(`Booking confirmed for ${tour.name}!\nWe've sent details to ${formData.email}`);
      onClose();
    } catch (error) {
      alert('Booking failed. Please try again.');
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* Travel Date */}
        <div>
          <label className="block mb-1 font-medium">Travel Date*</label>
          <DatePicker
            selected={formData.startDate}
            onChange={(date) => setFormData({...formData, startDate: date})}
            minDate={new Date()}
            className={`border p-2 rounded w-full ${errors.startDate ? 'border-red-500' : ''}`}
            required
          />
          {errors.startDate && <p className="text-red-500 text-sm mt-1">{errors.startDate}</p>}
        </div>

        {/* Number of Guests */}
        <div>
          <label className="block mb-1 font-medium">Guests*</label>
          <input
            type="number"
            name="guests"
            min="1"
            max="10"
            value={formData.guests}
            onChange={handleChange}
            className={`border p-2 rounded w-full ${errors.guests ? 'border-red-500' : ''}`}
          />
          {errors.guests && <p className="text-red-500 text-sm mt-1">{errors.guests}</p>}
        </div>
      </div>

      {/* Personal Information Section */}
      <div className="pt-2 border-t">
        <h4 className="font-medium mb-3">Personal Information</h4>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          {/* Full Name */}
          <div>
            <label className="block mb-1 font-medium">Full Name*</label>
            <input
              type="text"
              name="fullName"
              value={formData.fullName}
              onChange={handleChange}
              className={`border p-2 rounded w-full ${errors.fullName ? 'border-red-500' : ''}`}
            />
            {errors.fullName && <p className="text-red-500 text-sm mt-1">{errors.fullName}</p>}
          </div>

          {/* Email */}
          <div>
            <label className="block mb-1 font-medium">Email*</label>
            <input
              type="email"
              name="email"
              value={formData.email}
              onChange={handleChange}
              className={`border p-2 rounded w-full ${errors.email ? 'border-red-500' : ''}`}
            />
            {errors.email && <p className="text-red-500 text-sm mt-1">{errors.email}</p>}
          </div>

          {/* Phone */}
          <div>
            <label className="block mb-1 font-medium">Phone*</label>
            <input
              type="tel"
              name="phone"
              value={formData.phone}
              onChange={handleChange}
              placeholder="e.g. 254712345678"
              className={`border p-2 rounded w-full ${errors.phone ? 'border-red-500' : ''}`}
            />
            {errors.phone && <p className="text-red-500 text-sm mt-1">{errors.phone}</p>}
          </div>

          {/* Payment Method */}
          <div>
            <label className="block mb-1 font-medium">Payment Method*</label>
            <select
              name="paymentMethod"
              value={formData.paymentMethod}
              onChange={handleChange}
              className="border p-2 rounded w-full"
            >
              <option value="credit">Credit Card</option>
              <option value="mpesa">M-Pesa</option>
              <option value="paypal">PayPal</option>
              <option value="bank">Bank Transfer</option>
            </select>
          </div>
        </div>

        {/* Special Requests */}
        <div className="mt-4">
          <label className="block mb-1 font-medium">Special Requests</label>
          <textarea
            name="specialRequests"
            value={formData.specialRequests}
            onChange={handleChange}
            rows={3}
            className="border p-2 rounded w-full"
            placeholder="Dietary needs, accessibility requirements, etc."
          />
        </div>

        {/* Terms Agreement */}
        <div className="mt-4 flex items-start">
          <input
            type="checkbox"
            name="agreeToTerms"
            checked={formData.agreeToTerms}
            onChange={handleChange}
            className="mt-1 mr-2"
          />
          <label className="text-sm">
            I agree to the <a href="/terms" className="text-green-600 hover:underline">terms and conditions</a>*
            {errors.agreeToTerms && <p className="text-red-500 text-sm mt-1">{errors.agreeToTerms}</p>}
          </label>
        </div>
      </div>

      {/* Form Actions */}
      <div className="flex gap-2 pt-4">
        <button
          type="button"
          onClick={onClose}
          className="flex-1 border border-gray-300 text-gray-700 px-4 py-2 rounded hover:bg-gray-50 transition"
        >
          Cancel
        </button>
        <button
          type="submit"
          disabled={isSubmitting}
          className={`flex-1 bg-green-600 text-white px-4 py-2 rounded transition ${isSubmitting ? 'opacity-70' : 'hover:bg-green-700'}`}
        >
          {isSubmitting ? (
            <span className="flex items-center justify-center">
              <svg className="animate-spin -ml-1 mr-2 h-4 w-4 text-white" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24">
                <circle className="opacity-25" cx="12" cy="12" r="10" stroke="currentColor" strokeWidth="4"></circle>
                <path className="opacity-75" fill="currentColor" d="M4 12a8 8 0 018-8V0C5.373 0 0 5.373 0 12h4zm2 5.291A7.962 7.962 0 014 12H0c0 3.042 1.135 5.824 3 7.938l3-2.647z"></path>
              </svg>
              Processing...
            </span>
          ) : 'Confirm Booking'}
        </button>
      </div>
    </form>
  );
}